    monospace;
}

.rating {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-justify-content: center;
            justify-content: center;
    margin: -8px 0 -20px 15px;
}

.rating>input {
    display: none
}

.rating>label {
    position: relative;
    width: 19px;
    font-size: 25px;
    color: #FFD700;
    cursor: pointer
}

.rating>label::before {
    content: "\2605";
    position: absolute;
    opacity: 0
}

.rating>label:hover:before,
.rating>label:hover~label:before {
    opacity: 1 !important;
}

.rating>input:checked~label:before {
    opacity: 1
}

.rating:hover>input:checked~label:before {
    opacity: 0.4
}

.rating-view-only:hover>input:checked~label:before{
    opacity: 1 !important;
    cursor: not-allowed;
}

.rating-view-only>label:hover:before{
    opacity: 0 !important;
    cursor: not-allowed;
}


#updated-comment-form textarea{
