.dashboard a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.6s;
}

.dashboard .navbar {
    padding: 15px 10px;
    background: rgb(236, 229, 229);
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.479);
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
    display: flex;
    width: 100%;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar {
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    background: #b338d8;
    color: #fff;
    transition: all 0.6s;
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #c26add;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #b338d8;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}

#sidebar ul li a:hover {
    color: #b338d8;
    background: #fff;
}

#sidebar ul li.active>a,
a[aria-expanded="true"] {
    color: #fff;
    background: #b338d8;
}

a[aria-expanded="true"] {
    color: #fff;
    background: #b338d8;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #c26add;
}

.handleCollapseBtn{
    background-color: #b338d8;
    color: white;
    margin-top: -21px;
    margin-left: -31px;
}

.handleCollapseBtn:hover{
    background-color: #000000;
    color: #b338d8;
    transition: all 0.5s;
}

.components .nav-link, .custom-control-input, .custom-control-label{
    cursor: pointer;
}

    /* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
    width: calc(100% - 250px);
    padding: 7px;
    min-height: 100vh;
    transition: all 0.6s;
    position: absolute;
    top: 0;
    right: 0;
}

#content.active {
    width: 100%;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #content {
        width: 100%;
    }
    #content.active {
        width: calc(100% - 250px);
    }
    #sidebarCollapse span {
        display: none;
    }
}
